@mixin displayFlex($justify: unset, $align: unset, $direction: unset) {
  display: flex;
  justify-content: $justify;
  align-items: $align;
  flex-direction: $direction;
}

$colorAccent: #ffcc00;
$colorAccentBlueLight: #b6dfff;

$colorQuestionLight: #daeaf7;

body {
  padding: 0;
  margin: 0;
}

a {
  color: $colorAccentBlueLight;
}

.btn-primary {
  min-width: 20%;
  margin: 5px;
  padding: 10px;
  text-align: center;
  color: colorAccentBlueLight;
  font-size: 1.1em;
  border-radius: 10px;
  background-color: $colorAccent;
  border: none;
  cursor: pointer;
}

main {
  @include displayFlex(center, center);

  .loading-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    @include displayFlex(center, center, column);
    font-size: 2em;
    color: $colorQuestionLight;

    .loading {
      border: 16px solid #f3f3f3;
      border-radius: 50%;
      border-top: 16px solid #3498db;
      width: 120px;
      height: 120px;
      -webkit-animation: spin 2s linear infinite; /* Safari */
      animation: spin 2s linear infinite;
    }
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  //splash section
  .splash-container {
    @include displayFlex(center, center);
    width: 100%;
    height: 100vh;

    .bg-link {
      position: absolute;
      left: 10px;
      bottom: 10px;
    }

    .intro-container {
      @include displayFlex(center, center, column);
      width: clamp(20em, 20em, 25em);
      padding: 10px;
      color: $colorAccentBlueLight;
      background-color: #4d5b9e2c;

      h1 {
        color: $colorAccent;
        margin: 5px 5px;
      }

      div {
        margin-bottom: 10px;
      }

      .config-form {
        @include displayFlex(center, flex-start, column);
        margin-bottom: 20px;

        label {
          margin-bottom: 5px;
        }

        .config-field {
          display: block;
          width: 100%;
          padding: 10px 20px;
          outline: none;
          border: none;
          border-radius: 10px;
          font-size: 1em;
        }

        input[type="number"] {
          width: calc(100% - 40px);
        }
      }
    }
  }

  .background {
    width: 100%;
    height: 100vh;
    z-index: -1;
    position: fixed;
    top: 0;
    left: 0;
  }

  //main section
  .quiz-container {
    @include displayFlex(center, flex-start, column);
    min-width: 80%;
    padding: 20px;

    .quiz {
      border-bottom: thin solid $colorAccentBlueLight;
      padding: 10px 0px;
      width: 100%;
      .question {
        color: $colorQuestionLight;
        font-size: 1.25em;
        margin: 10px;
      }

      .answer-container {
        @include displayFlex(flex-start, center);
        margin: 5px;

        .answer {
          min-width: 10%;
          padding: 5px;
          margin: 0px 20px 0px 0px;
          text-align: center;
          color: white;
          font-size: 1.1em;
          border-radius: 10px;
          cursor: pointer;
        }
      }
    }

    .quiz-utils {
      @include displayFlex(center, center);
      width: 100%;
      min-height: 15vh;
      margin: 20px;

      h3 {
        color: white;
        font-weight: 400;
        margin: 0px 20px;
      }
    }
  }
}
