body {
  padding: 0;
  margin: 0;
}

a {
  color: #b6dfff;
}

.btn-primary {
  min-width: 20%;
  margin: 5px;
  padding: 10px;
  text-align: center;
  color: colorAccentBlueLight;
  font-size: 1.1em;
  border-radius: 10px;
  background-color: #ffcc00;
  border: none;
  cursor: pointer;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: unset;
}
main .loading-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 2em;
  color: #daeaf7;
}
main .loading-container .loading {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  /* Safari */
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
main .splash-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: unset;
  width: 100%;
  height: 100vh;
}
main .splash-container .bg-link {
  position: absolute;
  left: 10px;
  bottom: 10px;
}
main .splash-container .intro-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 20em;
  padding: 10px;
  color: #b6dfff;
  background-color: rgba(77, 91, 158, 0.1725490196);
}
main .splash-container .intro-container h1 {
  color: #ffcc00;
  margin: 5px 5px;
}
main .splash-container .intro-container div {
  margin-bottom: 10px;
}
main .splash-container .intro-container .config-form {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-bottom: 20px;
}
main .splash-container .intro-container .config-form label {
  margin-bottom: 5px;
}
main .splash-container .intro-container .config-form .config-field {
  display: block;
  width: 100%;
  padding: 10px 20px;
  outline: none;
  border: none;
  border-radius: 10px;
  font-size: 1em;
}
main .splash-container .intro-container .config-form input[type=number] {
  width: calc(100% - 40px);
}
main .background {
  width: 100%;
  height: 100vh;
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
}
main .quiz-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  min-width: 80%;
  padding: 20px;
}
main .quiz-container .quiz {
  border-bottom: thin solid #b6dfff;
  padding: 10px 0px;
  width: 100%;
}
main .quiz-container .quiz .question {
  color: #daeaf7;
  font-size: 1.25em;
  margin: 10px;
}
main .quiz-container .quiz .answer-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: unset;
  margin: 5px;
}
main .quiz-container .quiz .answer-container .answer {
  min-width: 10%;
  padding: 5px;
  margin: 0px 20px 0px 0px;
  text-align: center;
  color: white;
  font-size: 1.1em;
  border-radius: 10px;
  cursor: pointer;
}
main .quiz-container .quiz-utils {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: unset;
  width: 100%;
  min-height: 15vh;
  margin: 20px;
}
main .quiz-container .quiz-utils h3 {
  color: white;
  font-weight: 400;
  margin: 0px 20px;
}

